@import url("https://fonts.googleapis.com/css2?family=Space+Mono&display=swap");
* {
  padding: 0;
  margin: 0;
}

body {
  background: repeat url(./background.jpg);
  font-size: 0.66rem;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.8rem;
}

h3 {
  font-size: 1.4rem;
}

p {
  font-size: 0.8rem;
}

a {
  color: #ed613f;
  text-decoration: none;
  font-weight: normal !important;
}

nav {
  max-width: 70rem;
  background-color: #065f7e;
  padding: 0 0 1rem 2rem;
}
nav ul {
  padding: 2rem 1.5rem 0 0;
  display: flex;
  list-style: none;
}
nav ul li a {
  background-color: #ed613f;
  color: #f4f0bf;
  max-width: 8rem;
  text-align: center;
  font-weight: bolder;
  padding: 1rem 2rem 1rem 1.5rem;
}
nav ul li a:hover {
  background-color: #ed613f;
  color: white;
  max-width: 8rem;
  text-align: center;
  font-weight: bolder;
  box-shadow: 0 4px #5900ff;
  text-decoration: none;
  padding: 1rem 2rem 1rem 1.5rem;
}

header {
  max-width: 70rem;
  background-color: #ffc000;
  display: grid;
  gap: 2rem;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-items: center;
  background-position: 0% 12%;
  background-size: 100%;
  padding: 3rem 1.5rem 4rem 1.5rem;
}
header section {
  padding-left: 2rem;
  text-align: center;
}
header section h3 {
  color: #065f7e;
  padding-bottom: 1rem;
}
header section p {
  font-family: "Space Mono", monospace;
  color: #040050;
  line-height: 1.8;
  font-size: 0.9rem;
}
header img {
  padding: 0;
  margin: 0;
  max-width: 80%;
  border: 10px solid #ed613f;
}

.fourPlayers {
  padding: 1.5rem 2rem 1.5rem 2rem;
  max-width: 70rem;
  background-color: #ffc000;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
}
.fourPlayers .playerOne {
  background-color: #e68494;
  border-color: #742f3b;
}
.fourPlayers .playerOne button {
  background-color: #742f3b;
}
.fourPlayers .playerOne p {
  color: #742f3b;
}
.fourPlayers .playerOne .toDoList {
  background-color: #742f3b;
}
.fourPlayers .playerOne .titleColor {
  background-color: #742f3b !important;
  color: white;
}
.fourPlayers .playerOne input {
  background-color: #742f3b;
  color: white;
}
.fourPlayers .playerTwo {
  background-color: #5e7ce0;
  border-color: #303b5f;
}
.fourPlayers .playerTwo button {
  background-color: #303b5f;
}
.fourPlayers .playerTwo p {
  color: #303b5f;
}
.fourPlayers .playerTwo .toDoList {
  background-color: #303b5f;
}
.fourPlayers .playerTwo .titleColor {
  background-color: #303b5f !important;
  color: white;
}
.fourPlayers .playerTwo input {
  background-color: #303b5f;
  color: white;
}
.fourPlayers .playerThree {
  background-color: #92df92;
  border-color: #2a612a;
}
.fourPlayers .playerThree button {
  background-color: #2a612a;
}
.fourPlayers .playerThree p {
  color: #2a612a;
}
.fourPlayers .playerThree .toDoList {
  background-color: #2a612a;
}
.fourPlayers .playerThree .titleColor {
  background-color: #2a612a !important;
  color: white;
}
.fourPlayers .playerThree input {
  background-color: #2a612a;
  color: white;
}
.fourPlayers .playerFour {
  background-color: #dfdd82;
  border-color: #4d4c20;
}
.fourPlayers .playerFour button {
  background-color: #4d4c20;
}
.fourPlayers .playerFour p {
  color: #4d4c20;
}
.fourPlayers .playerFour .toDoList {
  background-color: #4d4c20;
}
.fourPlayers .playerFour .titleColor {
  background-color: #4d4c20 !important;
  color: white;
}
.fourPlayers .playerFour input {
  background-color: #4d4c20;
  color: white;
}

.medallas {
  padding: 1rem 0;
}
.medallas input {
  display: none;
}
.medallas label {
  display: contents;
}
.medallas label img {
  margin-left: 0.1rem;
  max-width: 12%;
}

.toDoList {
  background-color: #212529;
  padding: 1rem 0;
}
.toDoList label {
  margin-left: 1rem;
}
.toDoList .toDoNew {
  margin-left: 1rem;
}

.contadores {
  display: flex;
  gap: 3rem;
}
.contadores .tickets {
  display: flex;
  align-items: baseline;
  gap: 0.5rem;
  padding: 1rem 0 0 0;
}

.apiContainer {
  max-width: 70rem;
  background-color: #ffc000;
  padding: 0 2rem 0 2rem;
}
.apiContainer .batalla {
  background: no-repeat url(stadium.png);
  background-position: center center;
  background-size: 20%;
  display: flex;
  gap: 1rem;
  justify-content: space-around;
  border: 8px solid white;
  min-height: 27.4rem;
}
.apiContainer .batalla h3 {
  padding-bottom: 0.5rem;
  font-size: 1.2rem;
}
.apiContainer .batalla p {
  font-size: 0.6rem;
  line-height: 1rem;
}
.apiContainer .batalla img {
  width: 15rem;
}

.card {
  text-transform: capitalize;
}

.titleColor {
  background-color: #000000 !important;
  color: white;
}

.agregarTodo {
  padding-top: 0.5rem;
}

.m-auto {
  margin: 0 auto;
}

.reglamentoContainer {
  max-width: 70rem;
  background-color: #ffc000;
  padding: 1.5rem 2rem 3rem 2rem;
}
.reglamentoContainer .infoPDF {
  border: 8px solid #bf593e;
  background-color: #f4f3c3;
  display: flex;
  gap: 1rem;
  align-items: baseline;
  padding: 1rem 1rem 0.5rem 1rem;
  justify-content: space-between;
}
.reglamentoContainer .reglamento {
  overflow-y: scroll;
  max-height: 25rem;
  background: #bf593e;
  text-align: -webkit-center;
  padding: 1rem 0 1rem 0;
}

footer {
  background-color: #303030;
  padding-bottom: 2rem;
}
footer section {
  padding: 3rem 2rem 1rem 2rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  max-width: 70rem;
  align-items: center;
}
footer section .icons a {
  padding-right: 0.8rem;
  font-size: 1.5rem;
  opacity: 0.6;
}
footer section .icons a:hover {
  opacity: 1;
}
footer section .icons a i {
  font-size: 1.5srem;
}
footer section .textfooter {
  text-align: center;
}
footer section .textfooter p {
  color: white;
  font-size: 0.6rem;
}
footer hr {
  max-width: 70rem;
  margin: 0 auto;
}

@media screen and (max-width: 960px) {
  header {
    max-width: 70rem;
    background-color: #ffc000;
    display: inline-block;
    text-align: center;
    padding: 2rem 1.5rem 1rem 1.5rem;
  }

  .fourPlayers {
    display: block;
  }
  .fourPlayers .nes-container {
    margin-top: 1rem;
  }

  .batalla {
    display: grid !important;
    background-image: none !important;
    gap: 1rem;
  }
}

