@mixin reset-pm {
    padding: 0;
    margin: 0;
}

@mixin container {
    max-width: 70rem;
    background-color: #ffc000;
}

@mixin contPadding{
    padding: 1.5rem 2rem 1.5rem 2rem;
}

@mixin btn($background-color, $color) {
    background-color: $background-color;
    color: $color;
    max-width: 8rem;
    text-align: center;
    font-weight: bolder;
}

@mixin playersColor($background-color, $color){
    background-color: $background-color;
    border-color: $color;
    button{
      background-color: $color;
    }
    p{
      color: $color;
    }
    .toDoList{
      background-color: $color;
    }
    .titleColor {
      background-color: $color !important;
      color: white;
    }
    input {
      background-color: $color;
      color: white;
    }
}